import "./App.css";
import ImageSlide from "./components/ImageSlide";
import { Col, Row, Layout } from "antd";
import Navbar from "./layout/Navbar";
import love_floating from "./images/love-floating.json";
import Lottie from "react-lottie";
import { useState, useEffect } from "react";
function App() {
  // const isloding = true;
  const [isLoding, setLoading] = useState(false);
  const defaultOptions = {
    loop: true,
    autoplay: true,
    animationData: love_floating,
    rendererSettings: {
      preserveAspectRatio: "xMidYMid slice",
    },
  };
  useEffect(() => {
    setTimeout(() => {
      setLoading(true);
    }, 1000);
  }, []);

  return (
    <div className="wrapper-main">
      {!isLoding ? (
        <div
          style={{
            margin: "auto",
            width: "fit-content",
            height: "fit-content",
            position: "absolute",
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
          }}
        >
          <Lottie options={defaultOptions} height={300} width={300} />
        </div>
      ) : (
        // </Layout>
        <Layout
          style={{
            "background-color": "white",
          }}
        >
          <Row gutter={[16, 10]}>
            <Col span={24}>
              <Navbar />
            </Col>
          </Row>
          <Row>
            <Col span={1}></Col>
            <Col span={22}>
              <div style={{ padding: "15px" }}>
                {/* <img src={process.env.PUBLIC_URL + `/public_images/img2.jpg`}></img> */}
                <ImageSlide />
              </div>
            </Col>
            <Col span={1}></Col>
          </Row>
        </Layout>
      )}
    </div>
  );
}

export default App;
